<template>
    <div class="container pb-4">
        <h3 class="text-white mt-4 ">Projects</h3>
        <div class="shine-divider"></div>
        <div v-if="loadingStatus" class="mt-3">
            <Pending/>
        </div>
        <div class="row " v-if="!loadingStatus">
            <div v-for="project in projects" :key="project.id" class="col-md-4 mt-md-3 mb-5 mt-2">
                <div style="position: relative;border-radius:10px">
                    <span class="badge text-dark px-2 p-1 rounded mt-2 ml-2 border"
                        style="position: absolute;background-color: rgba(255, 255, 255,0.9);">
                        {{project.type}}
                    </span>
                    <div class="card__arrow px-3">
                        <a :href="project.url" target="_blank" style="cursor: pointer" class="text-white">
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                                class="bi bi-arrow-right" viewBox="0 0 16 16">
                                <path fill-rule="evenodd"
                                    d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8" />
                            </svg>
                        </a>
                    </div>

                    <img style="width: 100%; height: 200px;border-radius: 10px 10px 0px 0px;" :src="project.img_url">
                    
                    <div class="bg-white w-100 py-2 pl-3 border"
                        style="position: absolute;border-radius: 10px;bottom:-40px">
                        <a :href="project.url" target="_blank" style="cursor: pointer" class="text-dark">
                            <div class="d-flex align-items-center" style="gap:10px">
                                <img class="bg-dark"
                                    style="width: 40px; height: 40px; border-radius: 50%; object-fit: contain;"
                                    :src="project.logo_url" alt="Project Logo">
                                <h6 class="mt-2">{{project.name}}</h6>
                            </div>
                            <div class="pt-1 pb-1">
                                <span v-for="item in project.used" :key="item.id" class="badge text-white mr-1"
                                    style="background-color: #df8218;">
                                    {{item}}
                                </span>
                            </div>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Pending from "./../components/Pending.vue";
    export default {
        name: "Project",
        data() {
            return {
                loadingStatus: true,
                projects: []
            };
        },
        components: {
            Pending,
        },
        created() {
            fetch('https://shineshinedev.github.io/portfolio/data.json')
                .then(response => {
                    if (!response.ok) {
                        throw new Error('Network response was not ok');
                    }
                    return response.json();
                })
                .then(data => {
                    this.projects = data;
                    this.loadingStatus =false
                })
                .catch(error => {
                    console.error('There was a problem with the fetch operation:', error);
                });
        }
    };
</script>

<style scoped>
    .card__arc path {
        top: 0;
        fill: white;
        d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
    }

    .card__arrow {
        position: absolute;
        background: #df8218;
        border-radius: 0px 30px 0px 100px;
        right: 0;
        font-size: 19px;
        color: white;
        transition: 0.2s;
    }

    .shine-divider {
        margin-top: 5px;
        margin-bottom: 3px;
        border: 0px;
        height: 3px;
        background: linear-gradient(to right, rgba(157, 157, 157, 0), #ec4e10, rgba(0, 0, 0, 0));
        background-size: 200% 100%;
        animation: shineAnimation 1.5s infinite alternate;
    }

    @keyframes shineAnimation {
        0% {
            background-position: 100% 50%;
        }

        100% {
            background-position: 0 50%;
        }
    }
</style>